.App {
    text-align: center;
}

.main-container {
    /* padding: 5px; */
    margin: 10px auto;
    width: 98%;
    display: grid;
    grid-gap: 10px;
    padding-left: 10px;
    grid-template-rows: auto 1fr;
    grid-template-columns: 220px 85px 90px 90px 70px 160px 70px 70px 80px 90px 140px repeat(
            5,
            100px
        );
    background-color: rgb(245, 243, 243);
    color: rgb(109, 108, 108);
    overflow-x: scroll;
    transition: all 0.4s ease-in-out;
    position: relative;
}

.item {
    border-radius: 5px;
    border: 1px solid #ccc;
    /* background-color: rgb(25, 114, 155); */
    /* color: aliceblue; */
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.item-main {
    grid-column: 1 / -1;
    height: 30px;
    display: flex;
    justify-content: flex-start;
    font-weight: 600;
    color: aliceblue;
    background-color: #5d8bf4 !important;
    padding-left: 5px;
    transition: all 0.2s ease-in;
    cursor: pointer;
}

.item-0 {
    z-index: 1000;
}

.item-main span {
    position: sticky;
    left: 8px;
}

.subject-select {
    width: 50%;
    height: 40px;
    margin-left: 20px;
    font-size: 12px;
}

.sticky {
    position: sticky;
    left: 0;
    top: 0;
}

/******************** UTILITY CLASSES ***************************/

.bg--dark {
    background-color: rgb(25, 25, 112);
    font-weight: 600;
    color: #f1f1f1;
    position: sticky;
    left: 0;
    top: 0;
}

.accordion:hover {
    background-color: #4465b1 !important;
}

.item-0 {
    background-color: rgb(231, 230, 230);
    justify-content: left;
    padding-left: 10px;
    font-weight: 600;
}

.item-edit {
    margin: 0 5px 0 -5px;
    padding: 4px 8px;
    cursor: pointer;
    color: #fff;
    background-color: #ff1818;
    border: none;
    border-radius: 5px;
    transition: all 0.2s ease-out;
}

.item-edit:hover {
    background-color: #b61515;
}

/* #body {
    background-color: #f7f8f8;
    margin: 0 20px;
} */

.content {
    margin: 0 auto;
    margin-top: 20px;
    border-radius: 5px;
    padding: 10px;
    max-width: 1400px;
    background-color: #fff;
}

.content-form {
    padding-top: 20px;
}

.form-group {
    margin: 10px 0 30px 0;
}

.form-group span {
    color: rgb(160, 158, 158);
}

#body .form-group label {
    font-weight: 600;
    font-size: 12px;
    color: cornflowerblue;
}

#body .form-control {
    border-color: rgb(64, 228, 173);
    background-color: #f7f8f8;
    font-size: 12px;
}

#body .btn-save {
    padding: 6px 30px;
    font-size: 16px;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none !important;
    border: none;
    border-radius: 5px;
    box-shadow: none;
    background-color: rgb(65, 65, 65);
    color: #fff;
    transition: all 0.3s ease-in-out;
    transform: translate(0);
}

#body .btn-save:hover {
    background-color: rgb(0, 0, 0);
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
}

#body .btn-save:active {
    outline: none !important;
    border: none !important;
    border-color: none !important;
    transform: translateY(3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

/* ***********************************  Utility Classes *********************************** */

.margin-left {
    margin-left: -40px;
}

#body .active {
    font-weight: 600;
    color: rgb(226, 10, 75);
}

.flex-div {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 10px;
}

.flex-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    margin-left: -40px;
}

/* *************************** Delete Icon **************************** */

.margin-bottom {
    margin-bottom: 10px !important;
}

.button-right {
    display: flex;
    justify-content: flex-end;
}

#body .btn-reset {
    padding: 6px 30px;
    font-size: 16px;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none;
    box-shadow: none;
    transition: all 0.3s ease-in-out;
    border-radius: 5px;
    transform: translate(0);
    background-color: #fff;
    border: 1px solid #000;
    color: #000;
    margin-left: 10px;
}

#body .btn-reset:hover {
    background-color: rgb(65, 65, 65);
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid rgb(65, 65, 65);
    color: #fff;
    outline: none;
}

#body .btn-reset:active {
    outline: none;
    border-color: none;
    transform: translateY(3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.input {
    border: 1px solid #ccc;
    display: inline-block;
    font-family: inherit;
    min-width: 50px;
    font-size: inherit;
    padding: 1px 6px;
}

.auto {
    width: auto;
    position: relative !important;
}

.form-control {
    min-width: 150px !important;
    padding-right: 24px !important;
}

/* ******************* FILTER INPUTS *************************** */

.search-input {
    background-color: #f7f8f8;
    font-size: 12px;
    border: 1px solid rgb(64, 228, 173);
    border-radius: 5px;
    padding: 4px;
    margin-top: -4px;
    transition: all 0.2s ease-in;
}

.search-input:focus {
    border-color: rgb(64, 228, 173);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgb(64, 228, 173);
    outline: 0 none;
}

.btn-new {
    padding: 6px 30px;
    margin: 0 0 15px 15px;
    font-size: 14px;
    font-family: inherit;
    text-transform: uppercase;
    letter-spacing: 1px;
    outline: none !important;
    border: none;
    border-radius: 5px;
    box-shadow: none;
    background-color: #ff1818;
    color: #fff;
    transition: all 0.3s ease-in-out;
    transform: translate(0);
}

.btn-new:hover {
    background-color: #b61515;
    box-shadow: 4px 8px 10px rgba(0, 0, 0, 0.2);
}

.btn-new:active {
    outline: none !important;
    border: none !important;
    border-color: none !important;
    transform: translateY(3px);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
}

.no-data {
    width: 90%;
    margin: 40px;
    padding: 20px;
    background-color: #f0f1f1;
}

.no-data--paragraph {
    margin-left: 15px;
    font-size: 18px;
    color: #333;
}
